.contact-box {
    padding-top: .7cm;
}



.contact-buttons {
    margin-top: 1.5cm;
    display: flex;
    column-gap: .5cm;
    row-gap: .5cm;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
}

.email-me {
    padding: .45cm .9cm;
    border-radius: 100px;
    font-size: 20px;

}

.Contact {
    margin-bottom: 2cm;
}

.contact-desc {
    text-align: center;

    font-size: 18px;
    font-family: raleway;
    color: rgb(154, 154, 154);
}




/* CSS */
.button-64 {
    align-items: center;
    background-image: linear-gradient(90deg, rgb(230, 65, 87) 0%, rgb(65, 93, 230) 100%);
    border: 0;
    border-radius: 8px;
    box-shadow: rgba(151, 65, 252, 0.2) 0 15px 30px -5px;
    box-sizing: border-box;
    color: #FFFFFF;
    display: flex;
    font-family: Phantomsans, sans-serif;
    font-size: 20px;
    justify-content: center;
    line-height: 1em;
    max-width: 100%;
    min-width: 140px;
    padding: 3px;
    text-decoration: none;
    user-select: none;
    -webkit-user-select: none;
    touch-action: manipulation;
    white-space: nowrap;
    cursor: pointer;
}

.button-64-light {
    color: black;

    &:hover {
        color: white;
    }
}

.button-64:active,
.button-64:hover {
    outline: 0;
}

.contact-desc-light {
    color: black;
    font-weight: 550;
}

.button-64 span {
    background-color: #0f0f13;
    padding: 16px 24px;
    border-radius: 6px;
    width: 100%;
    height: 100%;
    transition: 300ms;
}

.button-64-light span {
    background-color: #ffffff;
    padding: 16px 24px;
    border-radius: 6px;
    width: 100%;
    height: 100%;
    transition: 300ms;
}

.button-64:hover span {
    background: none;
}

.mail-button {
    display: flex;
    justify-content: center;
    align-items: center;
}

.other-buttons {
    margin-top: 1cm;
    display: flex;
    justify-content: center;
    column-gap: .5cm;
    flex-wrap: wrap;
    row-gap: .3cm;
}

.cimg {
    width: 1.3cm;
    padding: .3cm .45cm;
    border: 2px solid #0f0f13;
    box-shadow: rgba(0, 0, 0, 0.500) 0px 2px 4px 0px, rgba(0, 0, 0, 0.500) 0px 4px 12px 4px;
    cursor: pointer;
    border-radius: 15px;

    &:hover {
        border: 2px solid #850dd5;
    }
}

.cimg-light {
    background-color: rgb(255, 255, 255);


    box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;

    border: 2px solid rgb(217, 217, 217);
}

@media (min-width: 768px) {
    .button-64 {
        font-size: 24px;
        min-width: 196px;
    }
}


@media only screen and (max-width : 1000px) {
    .cimg {
        width: 1.1cm;
        padding: .3cm .4cm;
    }

    .other-buttons {
        column-gap: .35cm;
    }

    .contact-desc {
        font-size: 16px;
    }
}

@media only screen and (max-width : 700px) {
    .cimg {
        width: 1cm;
        padding: .25cm .35cm;
    }

    .other-buttons {
        column-gap: .35cm;
    }
}

@media only screen and (max-width : 600px) {
    .cimg {
        width: .85cm;
        padding: .22cm .32cm;
    }

    .other-buttons {
        column-gap: .35cm;
    }
}

@media only screen and (max-width : 500px) {
    .cimg {
        width: .8cm;
        padding: .25cm .3cm;
    }

    .other-buttons {
        column-gap: .35cm;
    }
}





@media only screen and (max-width:600px) {
    .contact-desc {
        font-size: 15px;
    }

}

@media only screen and (max-width:500px) {
    .contact-desc {
        font-size: 14px;
    }
}

@media only screen and (max-width: 345px) {
    .button-64 {
        width: 80vw;
    }
}