.Nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: .5cm 0cm;
}


.logo {
    width: 1.2cm;
    height: 1.2cm;
    border-radius: 100px;
}

.menu-item-light {
    color: black;
    font-weight: 600;
}

.menu {
    display: flex;
    align-items: center;
    column-gap: .5cm;
}

.menuG {
    width: 1cm;
    height: 1cm;
    display: none;
}

.menu-item-nav {
    text-decoration: underline;
    text-decoration-color: green;
    text-decoration-thickness: 4px;
    text-underline-position: under;
    text-decoration-style: dashed;
}

.menu-item-navO {
    text-decoration: underline;
    text-decoration-color: green;
    text-decoration-thickness: 4px;
    text-underline-position: under;
    text-decoration-style: dashed;
}

.menu-item {
    font-size: 20px;
    font-family: kalam;
    cursor: pointer;
}

.menu-itemO {
    font-size: 20px;
    font-family: kalam;
    cursor: pointer;
}

.mode {
    width: .55cm;
    cursor: pointer;

}

.overlay-nav {

    position: fixed;

    bottom: 0;
    z-index: 100;
    top: 0;
    right: 0;
    backdrop-filter: blur(10px);
    display: flex;
    justify-content: flex-end;
    transition: .2s;
    width: 0%;
}

.close-button {
    padding: .4cm 1cm;
    border: thin solid rgb(64, 64, 64);
    font-size: 18px;
    border-radius: 500px;
    cursor: pointer;
    background-color: #0f0f13;
    color: white;
    text-align: center;

    &:hover {
        box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
        background-color: rgb(194, 12, 36);
        border: thin solid #0f0f13;
    }
}

.overlay-menu {
    padding-top: 1cm;
    width: 5cm;
    height: 100%;
    overflow-y: auto;
    border: 1px solid rgb(64, 64, 64);
    background-color: #0f0f13;
    display: flex;
    flex-direction: column;
    row-gap: .3cm;
    align-items: center;
    justify-content: flex-start;
}

@media only screen and (min-width : 600px) {
    .menuG {
        display: none;
    }

    #overlay-nav {
        width: 0%;
        display: none;
    }
}

@media only screen and (max-width:600px) {
    .menu-item {
        display: none;
    }

    .menuG {
        display: block;
    }
}


@media only screen and (max-width:500px) {
    .overlay-menu {
        width: 100%;
    }
}