.About {
    padding: 0cm 0cm 2cm 0cm;
}

.about-me-box {
    border: 2px solid #0f0f13;
    background-color: #0f0f13;



    padding: 0.1cm .5cm 0.5cm 0.5cm;
    border-radius: 25px;
    // box-shadow: rgba(60, 64, 67, 0.3) 0px 2px 4px 0px, rgba(60, 64, 67, 0.15) 0px 4px 12px 4px;

    &:hover {
        // border: 2px solid #850dd5;
    }

}

.about-me-box-light {
    background-color: #ffffff;
    border: 2px solid white;

}

.about-buttons {
    display: flex;
    align-items: center;
    column-gap: .3cm;
    flex-wrap: wrap;
    row-gap: .4cm;
}

.introduction {
    font-size: 35px;
    font-family: Titillium Web;
    font-weight: 600;
    letter-spacing: .02cm;
    color: #850dd5;
}

.introduction-light {
    color: #850dd5;
}

.about-det {
    font-size: 19px;
    font-family: Titillium Web;

    line-height: .7cm;
    // width: 100%;
    // color: #BA8ED8;
}

.about-det-light {
    color: black;
}

@media only screen and (max-width:1100px) {
    .introduction {
        font-size: 33px;
    }
}

@media only screen and (max-width:900px) {


    .about-det {
        font-size: 18px;
        line-height: .65cm;

    }

    .introduction {
        font-size: 31px;
        letter-spacing: .04cm;
    }


}

@media only screen and (max-width:800px) {

    .about-det {
        font-size: 17px;
        line-height: .6cm;
    }

    .introduction {
        font-size: 29px;
    }


}

@media only screen and (max-width:600px) {
    .about-det {
        font-size: 16.5px;
        line-height: .55cm;
    }

    .introduction {
        font-size: 28px;
        letter-spacing: .03cm;
    }

}



@media only screen and (max-width:450px) {

    .about-det {
        font-size: 16px;
        line-height: .53cm;
    }

    .introduction {
        font-size: 27px;
    }

}

@media only screen and (max-width:400px) {
    .introduction {
        font-size: 25px;
    }
}