.home-display {
    display: flex;
    margin-top: 1cm;

    justify-content: space-between;
    align-items: flex-start;
}

.Home {
    display: flex;
    flex-direction: column;

    justify-content: center;
    row-gap: 2.5cm;
}

.home-right {
    width: 15cm;
}

.backI {
    width: 9cm;
    height: auto;
    border-radius: 100px;
}

.home-buttons {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-top: .5cm;
    column-gap: .5cm;
    row-gap: .4cm;
}

.about-button {
    padding: .3cm .7cm;
    border: 2.5px solid rgb(65, 93, 230);
    font-size: 17px;
    border-radius: 500px;
    cursor: pointer;
    background-color: rgb(65, 93, 230);
    transition: .3s;
    color: white;
    text-align: center;

    &:hover {
        box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
        background-color: transparent;
        border: 2.5px solid rgb(65, 93, 230);
    }
}

.view-button {
    padding: .2cm .4cm;
    border: 2.5px solid rgb(65, 93, 230);
    font-size: 15px;
    border-radius: 500px;
    cursor: pointer;
    background-color: transparent;
    transition: .3s;
    color: white;
    text-align: center;

    &:hover {
        box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
        
        background-color: rgb(65, 93, 230);
        border: 2.5px solid rgb(65, 93, 230);
    }
}

.about-button-light,

.resume-button-light {
    color: white;

    &:hover {
        color: black;
    }
}
.view-button-light{
    color: black;

    &:hover{
        color: white;
    }
}

.resume-button {
    padding: .3cm .7cm;
    border: 2.5px solid rgb(230, 65, 87);
    font-size: 17px;
    border-radius: 500px;
    background-color: rgb(230, 65, 87);
    cursor: pointer;
    transition: .3s;

    color: white;
    text-align: center;

    &:hover {
        box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
        background-color: transparent;
        border: 2.5px solid rgb(230, 65, 87);
    }
}

.home-box {
    width: fit-content;
    display: flex;
    flex-direction: column;
    align-items: center;

}

.bio {
    position: relative;
    font-size: 25px;
    font-family: Titillium Web;
    margin-top: -1.2cm;

    line-height: .85cm;
    color: rgb(154, 154, 154);
    width: 100%;
}

.bio-light {
    color: black;
}

.ProjectsH {
    margin-top: .5cm;
}

.view-all-box {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin-top: 1cm;
}

.name {
    font-weight: 1000;
    font-size: 100px;
    font-family: changa;
    height: fit-content;
    background: linear-gradient(90deg, rgb(230, 65, 87) 0%, rgb(65, 93, 230) 100%);
    background-clip: text;
    -webkit-background-clip: text;
    color: transparent;
    margin: 0;
    padding: 0;

}

.name-light {
    background: linear-gradient(90deg, rgb(231, 53, 77) 0%, rgb(53, 82, 228) 100%);
    background-clip: text;
    -webkit-background-clip: text;
    color: transparent;
    margin: 0;
    padding: 0;
}

.projects-display-heading {
    font-size: 35px;
    text-align: center;
    color: white;
    font-family: Dancing Script;

}

.projects-display-heading-light {
    color: black;
    font-weight: 600;
}

